.home {
  padding: 20px calc(50% - 600px);
  min-height: calc(100vh - 64px - 62px);
}

/*轮播图*/
.home-carousel {
  margin-bottom: 20px;
  position: relative;

  &--banner {
    width: 100%;
    height: 350px;
    //margin: auto;
  }
  &--next,
  &--prev {
    position: absolute;
    top: 40%;
    z-index: 100;
    color: #ffffff;
    font-size: 50px;
  }
  &--prev {
    left: 20px;
  }
  &--next {
    right: 20px;
  }
}

/*文章列表*/
.article-item{
  margin-bottom: 10px;
  padding: 20px;
  background: #fff;
  list-style: none;

  .item-cont {
    display: flex;

    &--img {
      width: 30%;
      display: block;
      margin-right: 20px;
      border-radius: 5px;

      img {
        width: 100%;
      }
    }

    &--title {
      font-size: 24px;
      font-weight: bold;
      margin: 10px 0;
    }

    &--info {
      font-size: 16px;
      color: #888;
      line-height: 24px;
    }
  }

  .item-author {
    width: 100%;
    clear: both;
    margin: 20px 0 0 0;
    color: #838383;
    font-size: 14px;
    span{
      margin-right: 10px;

      a {
        color: #096;
      }
    }
    .lm {
      color: #096;
      i {
        color: #ff7c7c;
        margin-right: 5px;
      }
    }
    .time {
      i {
        color: #00cad4;
        margin-right: 5px;
      }
    }
    .more {
      float: right;
    }
  }
}

.home-slider {
  margin-left: 20px;
  background-color: transparent;
}

/*简介*/
.slider-about {
  overflow: hidden;
  background: #FFF url("https://6672-frist-ixdkl-1300617104.tcb.qcloud.la/blog/www/avatarBanner.png") no-repeat top center;

  &--avatar {
    margin: 80px auto 20px;
    width: 100px;
  }
  &--name {
    color: #3f3f3f;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 10px;
    text-align: center;
  }
  &--position {
    color: #1abc9c;
    text-align: center;
  }
  &--text {
    padding: 20px 40px;
    color: #9a9a9a;
    line-height: 26px;
  }
}

/*搜索*/
.slider-search {
  background: #FFF;
  overflow: hidden;
  padding: 20px;
  margin: 20px 0;
}

/* 分类选择 */
.slider-classify {
  background: #FFF;
  overflow: hidden;
  padding: 20px;
  margin: 20px 0;

  &--select {
    width: 100%;
  }
}

/* 标签云 */
.slider-cloud {
  width: 100%;
  clear: both;
  overflow: hidden;
  background: #fff;
  margin-bottom: 20px;

  &--title {
    padding: 0 10px;
    line-height: 50px;
    height: 50px;
    font-size: 18px;
    border-bottom: 1px solid #e5e5e5;
    color: #333;
    position: relative;
    font-weight: bold;

    &:after {
      content: '';
      position: absolute;
      height: 2px;
      width: 0;
      right: inherit;
      top: inherit;
      left: 0;
      bottom: -1px;
      background: #333;
      transition: 2s ease all;
    }
    &:hover:after {
      width: 100%;
      transition: 2s ease all;
    }
  }
}
